















































































import { Component, Vue, Prop } from 'vue-property-decorator'
const AV = require('leancloud-storage')

@Component
export default class ContractForm extends Vue {
    private title = '意见反馈'
    private data = {
      标题: '',
      称呼: '',
      您的邮件地址: '',
      意见内容: ''
    }

    private handleContract() {
      var Forms = AV.Object.extend('MailForms')
      var formObject = new Forms()
      formObject.save({
        title: this.data.标题,
        name: this.data.称呼,
        email: this.data.您的邮件地址,
        message: this.data.意见内容
      }).then((object:any) => {
        this.$message('感谢您的提交，我们会尽快与您取得联系！')
        this.data.标题 = ''
        this.data.称呼 = ''
        this.data.您的邮件地址 = ''
        this.data.意见内容 = ''
      }).catch(() => {
        this.$message('提交意见失败!')
      })
    }
}
